import { useHead } from '@vueuse/head';
import { unref, computed } from 'vue';

const siteTitle = import.meta.env.VITE_APP_NAME;
const separator = '|';

export function usePageTitle(pageTitle: string) {
  useHead(
    computed(() => ({
      title: `${unref(pageTitle)} ${separator} ${siteTitle}`,
    }))
  );
}
